body {
    background-color: #444;
    color: #fff;
    text-align: center;
    font-size: 20px;
    font-family: 'Quicksand';
    padding-top: 5%;
    word-wrap: break-word;
}

.logo {
    width: 250px;
    height: 250px;
}

.App {
    display: flex;
    flex-direction: column;
    align-items: center; 
}
.WalletInfo {
    width: 500px;
}

.Block {
    border: 1px solid #fff;
    padding: 5%;
    margin: 5%;
}

.Transaction {
    padding: 5%;
}

.ConductTransaction, .TransactionPool { 
    margin: 10%;
    
}
a, a:hover { 
    color: #F32013 ;
    text-decoration: underline;
}